  .--btn {
    font-size: 1.6rem;
    font-weight: 400;
    padding: 6px 8px;
    margin: 0 5px 0 0;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
  }
  .--btn:hover {
    transform: translateY(-2px);
  }
  
  .--btn-lg {
    padding: 8px 10px;
  }
  
  .--btn-block {
    width: 100%;
  }
  
  .--btn-primary {
    color: #fff;
    background: #007bff;
  }
  .--btn-secondary {
    color: #fff;
    border: 1px solid #fff;
    background: transparent;
  }
  .--btn-danger {
    color: #fff;
    background: orangered;
  }
  
  .--btn-success {
    color: #fff;
    background: #28a745;
  }
/* Background */
.--bg-light {
    background: #fff;
  }
  .--bg-dark {
    background: var(--color-dark);
  }
  .--bg-primary {
    background: var(--color-primary);
  }
  .--bg-success {
    background: var(--color-success);
  }
  .--bg-grey {
    background: var(--color-grey);
  }
  
  .--form-control {
    font-size: 1.6rem;
    font-weight: 300;
  }
  
  .--form-control > * {
    margin: 5px 0;
  }

  .--form-control input {
    font-size: 1.6rem;
    font-weight: 300;
    padding: 8px 1rem;
    border: 1px solid #777;
    border-radius: 3px;
    outline: none;
  }
  .--form-control select {
    font-size: 1.4rem;
    font-weight: 400;
    padding: 8px 1rem;
    border: 1px solid #777;
    border-radius: 3px;
  }
  
  .--form-control label {
    font-size: 1.6rem;
    font-weight: 400;
    display: inline-block;
    min-width: 7rem;
    color: var(--color-dark);
    margin-right: 1rem;
  }
  
    
}

@media screen and (max-width: 600px) {
    .--flex-dir-column {
      flex-direction: column;
