@use '../../utilities/variables' as *;
@use '../../utilities/mixins' as *;

.fauxWoodBlinds {
        // text-align: center;
        flex-direction: column;
        justify-content: center;
        position: relative;
        // height: 1200px;
        .waveBlueLine{
            position: absolute;
            bottom: 0;
            z-index: 2;
        } 
        .container {
            // padding-top: 50px;
            padding-bottom: 120px;
            // z-index: 1;
            position: relative;
        }
        .text{

            .title{
                font-size: 37px;
            }
        }
        // .title {
        //     // color: #154a66;
        //     color: red;
        // }

        // }
        // .title{
        //     color: red;
        // }
   

        // h3 {
        //     color: #555;
        // }
        // p {
        //     margin: 0;
        // }
    }
    
    .container {
        margin-right: auto;
        margin-left: auto;
        padding-left: 10px;
        padding-right: 10px;
        display: flex;
    }
    
    
    .column {
        position: relative;
        min-height: 1px;
        padding-left: 10px;
        padding-right: 10px;
        
    }
    
    .text{
        text-align: center;
        max-width: 820px;
        margin: 0 auto;
        padding: 0 32px 14px;
        .title{
            // color:#154a66;
            color: red;
            font-size: 40px;
            text-align: center;
            margin-top: 70px;
        }
    }
    
    .home-product {
        font-size: 0;
        border-top: 1px solid #ccc;
    }
    
    .img-holder {
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        text-align: right;
    }
    
    .product-img {
        margin-top: 10px;
        margin-bottom: 10px;
        max-width: 100%;
        height: auto;
    }
    
    .detail {
        display: inline-block;
        vertical-align: middle;
        width: 30%;
        text-align: left;
        padding: 0 10px;
    }
    
    h3 {
        margin: 0;
        font-size: 18px;
        font-weight: 700;
    }
    
    @media (min-width: 768px) {
        .container {
            h2 {
                color: #154a66;
                font-size: 30px;
            }
            h3 {
                margin: 0 0 7px;
                font-size: 24px;
                color: #009ddc;
            }
        }
    
    
        .item {
            width: 33.33%;
            float: left;
        }
    
        .home-product {
            border: 0;
        }
    
        .img-holder {
            display: block;
            width: auto;
            margin: 0 0 11px;
            text-align: center;
        }
    
        .detail {
            width: auto;
            display: block;
            text-align: center;
        }
        
    }
    
    @media (min-width: 1024px) {
        .container {
            width: 960px;
            p {
                font-size: 16px;
            }
        }
    
        .text {
            font-size: 16px;
            padding: 0 0 25px;
            margin: 0 auto;
        }
    
    }
    
    
    
    